import { Image, Button } from "react-bootstrap"

import discord from "../Assets/discord.png"
import skype from "../Assets/skype.png"
import gmail from "../Assets/gmail.png"

const Address = () => {
    return (
        <div className="contact mt-20 mb-20">
            <div className="container flex flex-around">
                <div className="p-10" style={{maxWidth:"50%"}}>
                    <h5>Clients</h5>
                    <h3 className="text-justify">DM me if you're interested. </h3>
                </div>
                <div className="p-10" style={{maxWidth:"50%"}}>
                    <p className="text-justify mt-20">Don't spend your time on creating additional pages and components. We create everything for you. Customize and deploy website!</p>                    
                </div>
            </div>
            <div className="container flex flex-around mt-20">
                <Button href="https://discord.gg/HQr96mvh" variant="light" className="p-10 text-center">
                    <Image src={discord} className="w-200"/>
                </Button>
                <Button href="https://join.skype.com/invite/wF8gZhqkO1tA" variant="light" className="p-10 text-center">
                    <Image src={skype} className="w-100p"/>
                </Button>
                <Button href="mailto:kosharavitalii@gmail.com" variant="light" className="p-10 text-center">
                    <Image src={gmail} className="w-100p"/>
                </Button>
            </div>
        </div>
    )
}

export default Address