import { Image, Button } from "react-bootstrap"

import phone from "../Assets/phone.png"
import service from "../Assets/page.png"
import dashboard from "../Assets/adjustment.png"

const Summary = () => {
    return (
        <div className="summary">
            <div className="container flex">
                <div className="p-10" style={{maxWidth:"50%"}}>
                    <h5>Who am I?</h5>
                    <h3 className="text-justify">A well-rounded hands on engineer with significant depth in a diverse JS stack. </h3>
                </div>
                <div className="p-10" style={{maxWidth:"50%"}}>
                    <p className="text-justify">I care about my customers, the problem I am solving and my product.  I am PASSIONATE about making meaningful contributions to the direction of the product in a collaboration and partnership with the extended team and CEOs.</p>
                    <p className="text-justify">My goal is to build significant systems and features, to think about how I can help your team meet objectives and timelines, to balance speed and tech debt and to ensure the long-term health of the team and systems.</p>
                </div>
            </div>
            <div className="container flex flex-between mt-40">
                <div style={{maxWidth: '300px'}}>
                    <div className="text-center"><Image src={phone} className="w-40"/></div>
                    <p className="mt-20">Create modern, smart, pixel-perfect, and fully responsive web pages using React.js.</p>
                </div>
                <div style={{maxWidth: '300px'}}>
                    <div className="text-center"><Image src={service} className="w-40"/></div>
                    <p className="mt-20">Create and provide backend services with Node and Express.</p>
                </div>
                <div style={{maxWidth: '300px'}}>
                    <div className="text-center"><Image src={dashboard} className="w-40"/></div>
                    <p className="mt-20">Build dashboards that users can manage their products and customers.</p>
                </div>
            </div>
            <div className="container flex flex-center mt-40">
                <Button variant="dark" className="button-warning">My Recent Works</Button>
            </div>
        </div>
    )
}

export default Summary