import { Image } from "react-bootstrap";

import Gif from "../Assets/coder.gif"
import WireFrame from "../Assets/wireframe.svg"
import CodeScreen from "../Assets/code-screen.png"
import IconReact from "../Assets/logo192.png"
import IconNode from "../Assets/node-logo.png"
import IconMongo from "../Assets/mongo-logo.png"

const IntroPage = () => {
    return (
        <div id="intro-page" className="intro-page">
            <div className="intro-container">
                <Image src={WireFrame} className="absolute intro-wireframe"/>
                <Image src={Gif} className="intro-gif"/>
                <div>
                    <h5>Hi! I am a </h5>
                    <div className="intro-mask">
                        <h1>Full Stack Web Developer</h1>
                        <div className="intro-title-mask"></div>
                        <div className="intro-icon-react absolute">
                            <Image src={IconReact}/>
                        </div>
                        <div className="intro-icon-node absolute">
                            <Image src={IconNode}/>
                        </div>
                        <div className="intro-icon-mongo absolute">
                            <Image src={IconMongo}/>
                        </div>                    
                        <div className="intro-code-screen absolute">
                            <Image src={CodeScreen}/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default IntroPage;